import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import { Storage } from "@ionic/storage";
import { LoaderService } from "../../services/loader.service";
import { Router } from "@angular/router";
import { ToastService } from "../../services/toast.service";
import { ApiService } from "../../services/api.service";
import { AuthService } from "../../services/auth.service";
import { Location } from "@angular/common";
import * as moment from 'moment';
import {Platform, MenuController, ActionSheetController, PopoverController} from "@ionic/angular";
import { Device } from "@ionic-native/device/ngx";
import { AnalyticsService } from "../../services/analytics.service";
import { CommonService } from "../../services/common.service";
import { EventProviderService } from "../../services/event-provider.service";
import { TranslateService } from "@ngx-translate/core";
import {DomSanitizer} from "@angular/platform-browser";
import {environment} from "../../environments/environment";
import {Camera, CameraOptions } from "@ionic-native/camera/ngx";
// import { Base64 } from '@ionic-native/base64/ngx';
import {FilterPopoverComponent} from "../events/filter-popover/filter-popover.component";
import {ImagesLibraryComponent} from "../common/images-library/images-library.component";
import { ModalController} from '@ionic/angular';
import {BibleFilterModalComponent} from "../common/bible-filter-modal/bible-filter-modal.component";
// import {clearInterval} from "timers";

@Component({
    selector: 'app-profile',
    templateUrl: './profile.page.html',
    styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
    @ViewChild('fileIn', {read: false, static: false}) fileIn: ElementRef;
    guest: boolean = true;
    auth: any;
    profile_detail: any;
    first_name: any;
    last_name: any;
    phone: any;
    email: any;
    gender: any;
    dob: any;
    life_stage: any;
    is_volunteer: any;
    profile_picture_file: any;
    profile_picture: any;
    profile_picture_to_display: any;
    auth_obj: any;
    enable_home: any;
    start_time_analytics: any;
    page_name: any;
    custom_name: any;
    user: any;
    theme: any;
    alertMessage: any;
    page_in_home_tabs: any = false;
    is_cordova: boolean = false;
    image_uploaded_from_app: boolean = false;
    image_uploaded_from_pwa: boolean = false;
    cameraOptions: CameraOptions = {
        quality: 100,
        destinationType: this.camera.DestinationType.DATA_URL,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE
    };

    type: any = '';


    constructor(
        private storage: Storage,
        private loader: LoaderService,
        private router: Router,
        private toast: ToastService,
        private apiService: ApiService,
        private authService: AuthService,
        private location: Location,
        private common: CommonService,
        private event_provider: EventProviderService,
        private analytics: AnalyticsService,
        private platform: Platform,
        private device: Device,
        private translate: TranslateService,
        private menu: MenuController,
        private dom: DomSanitizer,
        public actionSheetController: ActionSheetController,
        private camera: Camera,
        // private base64: Base64,
        private modalCtrl: ModalController,
    ) {
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000)

        // chekcing if page is opened in home tabs or simple
        let current_url = new URL(window.location.href);
        let page_href = current_url.pathname;
        if (page_href.includes('home-tabs')){
            this.page_in_home_tabs = true;
        } else {
            this.page_in_home_tabs = false;
        }
    }

    ngOnInit() {
        // getting user from local storage
        this.storage.get('user').then(async (val) => {
            if ((val != null) && (val.user != null && val.user != "guest")) {
                // ============= assigning DATA if user is logged IN ==================== //
                this.auth_obj = val;
                this.auth = val.auth;
                this.profile_detail = val.user;
                this.first_name = this.profile_detail.first_name;
                this.last_name = this.profile_detail.last_name;
                this.phone = this.profile_detail.phone;
                this.email = this.profile_detail.email;
                this.gender = this.profile_detail.gender;
                this.dob = this.profile_detail.birthday;
                this.life_stage = this.profile_detail.life_stage;
                this.is_volunteer = this.profile_detail.is_volunteer;
                this.profile_picture_file = this.profile_detail.photo_profile ? this.profile_detail.photo_profile : '';
                this.profile_picture = this.profile_detail.photo_profile ? this.dom.bypassSecurityTrustUrl(this.profile_detail.photo_profile) : '';
               
                // ============= assigning DATA if user is logged IN ==================== //

            } else {
                this.guest = true;

                // navigating to login if not Logged IN
                this.router.navigate(['/login']);
            }
        });

        if (this.platform.is('cordova')) {
            this.is_cordova = true;
        }

        // event subscription, runs when event fired from common service to update if HOME Page is enabled or not
        this.event_provider.isHomeEnable.subscribe(value => {              // listening event
            this.enable_home = value.value;
        });
        if (!this.enable_home) {
            // updating home page status from variable set in common service
            // "enable_home" variable is used to switch ON or OFF the back button on Mobile View
            // If the HOME page is disabled and THIS page is selected as first landing page, there should be no BACK button
            this.enable_home = this.common.is_home_enable;
        }

        this.getDesign();
    }

    // onImgError
    // onImgError(event){
    //     event.target.src = this.dom.bypassSecurityTrustUrl(this.IMAGE_BASE_URL + this.profile_detail.photo_profile);
    //    //Do other stuff with the event.target
    //    }

    getPicture(opts){
        this.camera.getPicture(opts).then((imageData) => {
            imageData = 'data:image/jpeg;base64,' + imageData;
            this.image_uploaded_from_app = true;
            this.profile_picture = imageData ? this.dom.bypassSecurityTrustResourceUrl(imageData) : "";
            this.type = 'camera';
        }, (err) => {
            console.log("ERROR", err);
            // Handle error
        });
    }

    getCameraOptions(sourceType){
        if (sourceType === 'camera'){
           this.cameraOptions.sourceType = this.camera.PictureSourceType.CAMERA;
        } else if (sourceType === 'library'){
           this.cameraOptions.sourceType = this.camera.PictureSourceType.PHOTOLIBRARY;
        }

        return this.cameraOptions;
    }

    removeProfileImage(){
        this.profile_picture = '';
        this.profile_picture_file = '';
    }

    removeProfileImagePwa(event){
        event.stopPropagation();
        this.profile_picture = '';
        this.profile_picture_file = '';
    }

    async presentActionSheet() {
        let buttons =  [{
            text: 'Camera',
            icon: 'camera',
            handler: () => {
                let options = this.getCameraOptions('camera');
                this.getPicture(options);
            }
        }, {
                text: 'Upload',
                icon: 'folder',
                handler: () => {
                        let options = this.getCameraOptions('library');
                        this.getPicture(options);
                }
            },
            {
                text: 'Library',
                icon: 'images',
                handler: () => {
                    this.libraryPopover();
                    // let options = this.getCameraOptions('library');
                    // this.getPicture(options);
                }
            },{
                text: 'Cancel',
                icon: 'close',
                role: 'cancel',
                handler: () => {
                }
            }
        ];
        let remove_button = {
            text: 'Remove',
                role: 'destructive',
                icon: 'trash',
                handler: () => {
                this.removeProfileImage();
            }};


        if (this.profile_picture){
            buttons.unshift(remove_button);
        }

        const actionSheet = await this.actionSheetController.create({
            cssClass: 'my-custom-class',
            buttons: buttons
        });
        await actionSheet.present();

        const { role } = await actionSheet.onDidDismiss();
        console.log('onDidDismiss resolved with role', role);
    }

    async libraryPopover() {
        const modal = await this.modalCtrl.create({
            component: ImagesLibraryComponent,
            cssClass: 'BibleModal',
            componentProps: {
            }
        });
        modal.onDidDismiss().then(res => {
            console.log(res);
            console.log(res.data);
            if (res.data.selected_image){
                let image_data = res.data.selected_image;
                this.profile_picture = image_data.url;
                this.profile_picture_file = image_data.filename;
                this.type = 'photo_library';
                console.log("ttttt", this.type , image_data, this.profile_picture, this.profile_picture_file)
            }
        });
        return await modal.present();
    }

    onFileChanged(event) {
        const file = event.target.files[0];
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
            this.profile_picture = reader.result ? reader.result : '';
            console.log(this.profile_picture);
            this.image_uploaded_from_pwa = true;
            this.type = 'pwa';
        };
    }

    update_profile() {
        let prof_pic;
        if(this.type != "photo_library"){
            if (this.profile_picture && this.is_cordova && this.image_uploaded_from_app){                                // Image uploaded from app
                prof_pic = this.profile_picture['changingThisBreaksApplicationSecurity'];
            } else if (this.profile_picture && this.image_uploaded_from_pwa) {                                           // Image uploaded from PWA
                prof_pic = this.profile_picture;
            }  else if (!this.image_uploaded_from_pwa && !this.image_uploaded_from_app){                                 // Image not uploaded at all
                prof_pic = this.profile_picture_file;
            }
        }
        

        // preparing data to update profile
        const body = JSON.stringify({
            auth: 'Bearer ' + this.auth.access_token,
            first_name: this.first_name,
            last_name: this.last_name,
            email: this.email,
            gender: this.gender,
            life_stage: this.life_stage,
            birthday: this.dob,
            is_volunteer: this.is_volunteer,
            account_donor_id: this.profile_detail.id,
            photo_profile:  this.type == "photo_library" ? this.profile_picture : prof_pic,
            church_id: this.theme.ch_id,
            type: this.type
        });
        this.loader.presentLoading().then(() => {
            this.apiService.update_profile(body).subscribe(async (res: any) => {
                if (res.status) {
                    this.auth_obj.user = res.account_donor;

                    // updating user data in local storage
                    localStorage.setItem('auth', this.auth_obj.auth.access_token);
                    localStorage.setItem('refresh_token', this.auth_obj.auth.refresh_token);
                    
                    this.storage.set('user', this.auth_obj).then(() => {
                        this.ngOnInit();
                        this.analytics.startAnalytics();
                        this.event_provider.updateSideMenu();
                        this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.profile_updated : 'Profile Updated Successfully'), (this.alertMessage ? this.alertMessage.Success : 'Success'), 'success');
                    });
                    this.image_uploaded_from_pwa = false;
                    this.image_uploaded_from_app = false;
                } else if (!res.status) {
                    this.toast.presentToastWithOptions(res.message, (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
                }
                this.loader.stopLoading();
            })
        })
    }

    // navigating to back page
    gotobackPage() {
        if(this.theme.ch_id === '1000'){
            this.menu.toggle();
        } else {
            this.location.back();
        }
    }

    getDesign(){
        // getting theme from local storage
        this.storage.get('theme').then(async res => {
            this.theme = res;
            this.getAppPages()
        });
    }

    getAppPages(){
        this.storage.get('app_pages').then(res => {
            if (res){
                // getting pagename for analytics
                let feed_page = res.find(x => x.name === 'Profile');
                if (feed_page){
                    this.page_name = feed_page.name;
                    this.custom_name = feed_page.title;
                }else {
                    this.page_name = 'Profile';
                    this.custom_name = 'Profile';
                }
            }
        });
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data

            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }


        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.profile_detail ? this.profile_detail.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.theme.ch_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }
    ionViewWillEnter() {
        this.ngOnInit();
      
        this.event_provider.getChurchData.subscribe(async (res: any) => {
            if (!(Object.keys(res).length === 0)) {
                // asking for password
                let app_pages = res.apppages.find(x => x.name === 'Profile');
                if (app_pages && app_pages.password_protection_toggle === 'true') {
                    this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
                }
            }
        });
      
      }
    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
        // setting start time for analytics
        this.start_time_analytics = moment();
        this.event_provider.hidefooter(true, 'other');
    }

}
